<template>
  <div class="searchlist">
    <div class="list">
            <div class="search">
                <form @submit="search()">
                    <input type="search" class="search_box" @focus="isState=true" v-model="keyword" :class="{focus:keyword!=undefined&&isState==true}" placeholder="想吃点什么呢？">
                </form>
                <span class="ico iconfont icon-sousuo1"></span>
                <span class="iconfont icon-guanbi ico_x" v-show="keyword!=undefined&&keyword!=''" @click="keyword=''"></span>
                <span class="cancle two_t_m" v-show="keyword!=undefined&&isState==true" @click="keyword=undefined;isState=false;">取消</span>
            </div>
          <ul class="clearfix">
              <li class="item fl" v-for="i in 1" :key="i">
                <a href="#">
                  <img src="../../assets/test/goods.png" alt="">
                  <p>
                    <span class=" price red_love">￥12.00/盒</span>
                    <span class=" iconfont icon-aixin grey_love ico_show ico_show" @click.stop="collect()" :class="{red_love : isCollect}"></span>
                    <span class=" iconfont icon-aixin grey_love ico_show ico_hide" @click.stop="collect()" :class="{red_love : isCollect, fadeOutUp:isCollect}"></span>
                  </p>
                  <span class="gouwudai iconfont icon-gouwudai"></span>
                </a>
                <p class="describe two_t_h">芒果酸奶</p>
              </li>
          </ul>
      </div>
  </div>
</template>

<script>
export default {
  name: "Searchlist",
  data() {
    return {
      isCollect: false,
      keyword: undefined,
      isState: false,
    };
  },
  methods: {
    //键盘回车事件
    search() {
      alert(this.keyword);
    },
    collect() {
      this.isCollect = !this.isCollect;
    }
  },
  components: {}
};
</script>

<style scoped lang="less">
// 商品列表
.list {
  // 搜索框
  .search {
    font-size: 0.16rem;
    position: relative;
    margin: 0.3rem 0rem;
    .search_box {
      width: 3.2rem;
      height: 0.4rem;
      border: none;
      line-height: 0.41rem;
      background: rgba(0, 0, 0, 0.05);
      border-radius: 0.06rem;
      padding-left: 0.43rem;
      padding-right: 0.35rem;
    }
    .focus {
      width: 2.8rem;
      transition: width 0.3s;
    }
    .ico_x {
      font-size: 0.18rem;
      color: #ccc;
      position: absolute;
      top: 0.11rem;
      right: 0.55rem;
    }
    .cancle {
      border: none;
      position: absolute;
      top: 0.08rem;
      right: 0rem;
    }
    .ico {
      position: absolute;
      top: 0.11rem;
      left: 0.15rem;
      color: rgba(0, 0, 0, 0.5);
      font-size: 0.18rem;
    }
  }
  .item {
    margin-bottom: 0.2rem;
    &:nth-child(even) {
      margin-left: 0.2rem;
    }
    a {
      width: 1.5rem;
      height: 1.5rem;
      display: block;
      position: relative;
      border-radius: 0.12rem;
      overflow: hidden;
      img {
        width: 100%;
        height: 100%;
        display: block;
        background-color: hotpink;
      }
      p {
        font-size: 0.16rem;
        position: absolute;
        bottom: 0rem;
        left: 0rem;
        padding: 0rem 0.15rem 0.1rem 0.1rem;
        width: 100%;
        .price {
          font-family: PingFangSC-Regular;
          font-size: 0.12rem;
          color: #e7290f;
          letter-spacing: 0;
        }
        .ico_show {
          position: absolute;
          top: 0rem;
          right: 0.15rem;
        }
        .ico_hide {
          position: absolute;
          top: 0rem;
          right: 0.15rem;
        }
      }
      .gouwudai {
        position: absolute;
        top: 0.1rem;
        right: 0.17rem;
      }
    }
    .describe {
      margin-top: 0.05rem;
    }
  }
}
</style>



